const setInstance = () => {
    let cookie = document.cookie.split(';')
    let api = ''

    for(let i=0; i < cookie.length; i++){
        let cookieParts = cookie[i].split('=')
        let cookieName = cookieParts[0]
        let cookieValue = cookieParts[1]

        if(cookieName.replaceAll(" ", "") === 'api') api = window.atob(cookieValue)
        console.log(cookieParts)
    }

    return axios.create({
        headers: {
            'API-KEY': api
        },

        baseURL: 'https://social-network.samuraijs.com/api/1.0/', withCredentials: true
    })
}

let instance = setInstance()

export const usersAPI = {

    getArrayUsers(currentPage: number, pageSize: number) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`).then(response => {
            return response.data
        })
    },

    doFollow(userId: number) {
        return instance.post('follow/' + userId)
    },

    doUnfollow(userId: number) {
        return instance.delete('follow/' + userId)
    }

}

export enum ResultCodeProfile {
    success = 0,

    error = 1
}

export const profileAPI = {
    getProfileInformation(userId: number) {
        return instance.get<profileState>(`profile/` + userId).then(response => response.data)
    },

    putProfileInformation(allInfo: profileState) {
        return instance.put<profileState>(`profile/`, allInfo).then(response => response.data)
    },

    getProfileStatus(userId: number) {
        return instance.get<any>(`profile/status/` + userId).then(response => response.data)
    },

    putProfileStatus(newStatus: string) {
        return instance.put<string, string>(`profile/status`, {status: newStatus})
    },

    putProfilePhoto(photoFile: File) {
        let formData = new FormData()
        formData.append('image', photoFile)
        return instance.put<{ photos: { small: string, large: string }, resultCode: number }>(`/profile/photo`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
    }

}
export const chatAPI = {
    getAllDialogs() {
        return instance.get<any>('dialogs').then(response => response.data)
    },

    getDialogsWithID(userId: any) {
        return instance.get<any>('dialogs/' + userId + '/messages/').then(response => response.data)
    },

    putRefreshChatWithID(userId: any) {
        return instance.put<any>(`dialogs/` + userId).then(response => response.data)
    },

    postSendMessageToID(userId: any, message: any) {
        return instance.post<any>('dialogs/' + userId + '/messages/', {body: message}).then(response => response.data)
    },

    getNewMessageCount() {
        return instance.get<any>('dialogs/messages/new/count').then(response => response.data)
    },

    getMessageIsVievew(messageId: any) {
        return instance.get<any>('dialogs/messages/' + messageId + '/viewed').then(response => response.data)
    },

    postMessageToSpam(messageId: any) {
        return instance.post<any>('dialogs/messages/' + messageId + ' /spam').then(response => response.data)
    },

    deleteMessageId(messageId: any) {
        return instance.delete<any>('dialogs/messages/' + messageId).then(response => response.data)
    },

    putRestoreMessageId(messageId: any) {
        return instance.put<any>('dialogs/messages/' + messageId + '/restore').then(response => response.data)
    },

    getMessageNewestThenDate(userId: any, date: any) {
        return instance.get<any>('dialogs/' + userId + '/messages/new?newerThen=' + date).then(response => response.data)
    },

}

export const authAPI = {

    getAuthInformation() {
        return instance.get(`auth/me`)
    },

    doLogin(email: string, password: string, rememberMe: boolean = false) {

        return instance.post(`auth/login`, {email, password, rememberMe})

    },

    doLogout() {
        return instance.delete(`/auth/login`)
    },

    setAPIKey(APIKey: string) {
        // APIKeyString = APIKey
        if(APIKey.length === 36){
            document.cookie = `api=${window.btoa(APIKey)}`
            instance = setInstance()
        }
        // console.log(APIKeyString)
    }
}